.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: auto;
		height: 70%;
		.image-element {
			width: auto;
			height: 100%;
			img {
				width: auto;
				height: 100%;
			}
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: auto;
		height: 80%;
		.image-element {
			width: auto;
			height: 100%;
			img {
				width: auto;
				height: 100%;
			}
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 4rem;
		width: 180px;
		transform: translate(0, 20%);
		@include max-lg {
			left: 0;
		}
		@include max-md {
			width: 160px;
			transform: translate(-30%, 20%);
		}
		@include max-xs {
			width: 140px;
		}
		.image-element,
		img {
			transform-origin: 30% 100%;
		}
		.orn-4 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			transform: translate(40%, 20%);
			@include max-md {
				width: 160px;
			}
			@include max-xs {
				width: 140px;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 280px;
		transform: translate(30%, 20%) scaleX(-1);
		@include max-md {
			width: 180px;
			transform: translate(50%, -30%) scaleX(-1) rotate(20deg);
		}
		@include max-xs {
			width: 160px;
		}
		.image-element,
		img {
			transform-origin: 30% 100%;
		}
		.orn-6 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 200px;
			transform: translate(70%, 30%) scaleX(-1);
			@include max-md {
				width: 160px;
				transform: translate(30%, 40%) scaleX(-1) rotate(-20deg);
			}
			@include max-md {
				width: 140px;
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		padding: 2rem 3rem;
		margin-bottom: 3rem;
		@include max-md {
			aspect-ratio: 7/8;
		}
		@include max-xs {
			padding: 1rem 2rem;
		}
		.orn-10 {
			position: absolute;
			top: 0;
			right: 1.2rem;
			bottom: 0;
			left: 1.2rem;
			img {
				width: 100%;
				height: 100%;
			}
		}
		.image-element {
			width: 100%;
			height: 100%;
			background: $stroke-gradient;
			padding: 1px;
			mask: var(--mask);
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				mask: var(--mask);
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 1rem 5rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
