.header-section {
	position: relative;
	height: 100vh;
	width: 100%;
	overflow: hidden;
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
		rect {
			stroke: $stroke-gradient;
		}
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: auto;
		height: 100%;
		.image-element {
			width: auto;
			height: 100%;
			img {
				width: auto;
				height: 100%;
			}
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: auto;
		height: 90%;
		.image-element {
			width: auto;
			height: 100%;
			img {
				width: auto;
				height: 100%;
			}
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 4rem;
		width: 180px;
		transform: translate(0, 20%);
		@include max-lg {
			left: 0;
		}
		@include max-md {
			width: 160px;
			transform: translate(-30%, 20%);
		}
		@include max-xs {
			width: 140px;
		}
		.image-element,
		img {
			transform-origin: 30% 100%;
		}
		.orn-4 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			transform: translate(40%, 20%);
			@include max-md {
				width: 160px;
			}
			@include max-xs {
				width: 140px;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 280px;
		transform: translate(30%, 20%) scaleX(-1);
		@include max-md {
			width: 180px;
			transform: translate(50%, -30%) scaleX(-1) rotate(20deg);
		}
		@include max-xs {
			width: 160px;
		}
		.image-element,
		img {
			transform-origin: 30% 100%;
		}
		.orn-6 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 200px;
			transform: translate(70%, 30%) scaleX(-1);
			@include max-md {
				width: 160px;
				transform: translate(30%, 40%) scaleX(-1) rotate(-20deg);
			}
			@include max-md {
				width: 140px;
			}
		}
	}
	.border-gold {
		top: 1.2rem;
		left: 1.2rem;
		right: 1.2rem;
		bottom: 1.2rem;
		width: unset;
		height: unset;
		.background-element {
			width: 100%;
			height: 100%;
			img {
				object-fit: unset;
				width: 100%;
				height: 100%;
			}
		}
	}
	.header-content {
		position: relative;
		height: 100%;
		.header {
			position: relative;
			display: flex;
			justify-content: center;
			align-items: center;
			width: 50%;
			height: 100%;
			margin: 0 auto;
			padding: 4.5rem 0 0;
			.header-title {
				position: relative;
				text-align: center;
				&-content {
					position: relative;
					border-radius: var(--border-radius);
					.title,
					span {
						background: $stroke-gradient;
						background-clip: text;
						-webkit-text-fill-color: transparent;
					}
					.title {
						padding: 0.5rem;
					}
					p,
					span {
						background: $stroke-gradient;
						background-clip: text;
						-webkit-text-fill-color: transparent;
					}
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="rotate-left-custom"].has-animate {
	animation-name: rotateLeftCustom;
}
[data-anim="rotate-right-custom"].has-animate {
	animation-name: rotateRightCustom;
}

@keyframes rotateLeftCustom {
	0% {
		transform: rotate(75deg);
		opacity: 0;
	}
	40% {
		opacity: 1;
	}
	100% {
		transform: rotateZ(0);
	}
}

@keyframes rotateRightCustom {
	0% {
		transform: rotate(-75deg);
		opacity: 0;
	}
	40% {
		opacity: 1;
	}
	100% {
		transform: rotateZ(0);
	}
}

@keyframes zoomInMain {
	0% {
		transform: scaleZ(1);
	}
	100% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
}
