.stream-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	overflow: hidden;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: auto;
		height: 100%;
		.image-element {
			width: auto;
			height: 100%;
			img {
				width: auto;
				height: 100%;
			}
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: auto;
		height: 90%;
		.image-element {
			width: auto;
			height: 100%;
			img {
				width: auto;
				height: 100%;
			}
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 4rem;
		width: 180px;
		transform: translate(0, 20%);
		@include max-lg {
			left: 0;
		}
		@include max-md {
			width: 160px;
			transform: translate(-30%, 20%);
		}
		@include max-xs {
			width: 140px;
		}
		.image-element,
		img {
			transform-origin: 30% 100%;
		}
		.orn-4 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			transform: translate(40%, 20%);
			@include max-md {
				width: 160px;
			}
			@include max-xs {
				width: 140px;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 280px;
		transform: translate(30%, 20%) scaleX(-1);
		@include max-md {
			width: 180px;
			transform: translate(50%, -30%) scaleX(-1) rotate(20deg);
		}
		@include max-xs {
			width: 160px;
		}
		.image-element,
		img {
			transform-origin: 30% 100%;
		}
		.orn-6 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 200px;
			transform: translate(70%, 30%) scaleX(-1);
			@include max-md {
				width: 160px;
				transform: translate(30%, 40%) scaleX(-1) rotate(-20deg);
			}
			@include max-md {
				width: 140px;
			}
		}
	}
	.stream-wrapper {
		position: relative;
		border-radius: var(--border-radius);
		padding: 2rem;
		z-index: 5;
		@include max-lg {
			.row {
				align-items: center;
				gap: 2rem;
			}
		}
		@include max-md {
			padding: 2rem 1rem;
		}
		.stream-content {
			position: relative;
			.stream-description {
				position: relative;
				padding: 3rem 2rem;
				background: var(--color-primary);
				border-radius: var(--border-radius);
				border-bottom-left-radius: 0;
				border-bottom-right-radius: 0;
				@include max-md {
					padding: 2rem 1rem;
				}
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.card {
		background-color: transparent;
		height: 100%;
		color: var(--color-dark);
		border: none;
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		width: 100%;
		margin-top: 3rem;
		@include max-md {
			text-align: center;
		}
	}
}
