.couple-section {
	position: relative;
	text-align: center;
	.orn-1 {
		position: absolute;
		top: 50%;
		left: 0;
		width: 140px;
		transform: translate(-40%, -50%);
		z-index: 2;
		pointer-events: none;
		@include min-lg {
			width: 180px;
		}
		.image-element,
		img {
			transform-origin: 30% 100%;
		}
		.orn-2 {
			position: relative;
			width: 140px;
			transform: translate(0, -40%);
			@include min-lg {
				width: 180px;
			}
		}
	}
	.orn-3 {
		position: absolute;
		top: 40%;
		right: 0;
		width: 140px;
		transform: translate(40%, -50%) scaleX(-1);
		pointer-events: none;
		z-index: 2;
		@include min-lg {
			width: 180px;
		}
		.image-element,
		img {
			transform-origin: 30% 100%;
		}
	}
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin-bottom: 1rem;
		z-index: 3;
		span {
			position: relative;
			font-size: 48px;
			font-family: $font-secondary;
			background: $stroke-gradient;
			background-clip: text;
			-webkit-text-fill-color: transparent;
		}
	}
	.couple-wrapper {
		position: relative;
		padding: 5rem 0 0;
		@include max-md {
			padding: 3rem 0 0;
		}
		.image-wrapper {
			position: relative;
			width: 100%;
			height: auto;
			aspect-ratio: 16/9;
			padding: 2rem 3rem;
			margin-bottom: 3rem;
			@include max-md {
				aspect-ratio: 7/8;
				padding: 1rem 2rem;
			}
			.elements-absolute {
				top: 0;
				left: 1.2rem;
				right: 1.2rem;
				bottom: 0;
				width: unset;
				height: unset;
				background: $stroke-gradient;
				padding: 1px;
				mask: var(--mask);
				.background-element {
					height: 100%;
					width: 100%;
					background: var(--color-main);
					mask: var(--mask);
				}
			}
			.image-element {
				width: 100%;
				height: 100%;
				background: $stroke-gradient;
				padding: 1px;
				mask: var(--mask);
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
					mask: var(--mask);
				}
			}
		}
	}
	.couple-body {
		position: relative;
		padding: 0 1.2rem;
		margin-bottom: 3rem;
		.couple-content {
			position: relative;
			padding: 3rem 2rem;
			background: $stroke-gradient;
			mask: var(--mask);
			@include max-md {
				padding: 2rem 0;
			}
			&::before {
				position: absolute;
				top: 1px;
				right: 1px;
				bottom: 1px;
				left: 1px;
				background: var(--color-main);
				mask: var(--mask);
				content: "";
			}
			.elements-wrapper-absolute {
				top: 1px;
				right: 1px;
				bottom: 1px;
				left: 1px;
				width: unset;
				height: unset;
				mask: var(--mask);
			}
		}
	}
	.couple {
		position: relative;
		text-align: center;
		padding: 3rem 0;
		mask: var(--mask);
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background: var(--color-primary);
			opacity: 0.6;
			content: "";
		}
		.image-wrap {
			width: 80%;
			position: relative;
			aspect-ratio: 7/8;
			margin: 0 auto;
			@include max-md {
				width: 100%;
			}
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				padding: 1px;
				background: $stroke-gradient;
				mask: var(--mask);
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					mask: var(--mask);
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem 1rem 3rem;
			z-index: 5;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			margin: 0 auto;
			.sosmed {
				color: var(--color-light);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				background: var(--color-secondary);
				padding: 0.3rem 0.4rem;
				border-radius: 50px;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
