.venue-section {
	position: relative;
	padding: 5rem 0;
	background: var(--color-primary);
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: -1rem;
		left: 0;
		width: 240px;
		transform: translate(-40%, 0);
		@include max-md {
			top: -3rem;
			width: 180px;
			transform: translate(-50%, 0);
		}
		@include max-xs {
			width: 160px;
		}
		.image-element,
		img {
			transform-origin: 30% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 200px;
		transform: translate(30%, 0) scaleX(-1);
		@include max-md {
			top: 2rem;
			width: 160px;
			transform: translate(40%, 0) scaleX(-1);
		}
		@include max-xs {
			width: 140px;
		}
		.image-element,
		img {
			transform-origin: 30% 100%;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		padding: 2rem 3rem;
		margin-bottom: 3rem;
		@include max-md {
			padding: 1rem 2rem;
			aspect-ratio: 7/8;
		}
		.border-gold {
			position: absolute;
			top: 0;
			right: 1.2rem;
			bottom: 0;
			left: 1.2rem;
			background: $stroke-gradient;
			padding: 1px;
			mask: var(--mask);
			&-body {
				position: relative;
				width: 100%;
				height: 100%;
				background: var(--color-primary);
				mask: var(--mask);
			}
		}
		.image-element {
			width: 100%;
			height: 100%;
			background: $stroke-gradient;
			padding: 1px;
			mask: var(--mask);
			.venue-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				mask: var(--mask);
			}
		}
	}
	.venue-wrapper {
		position: relative;
		.venue-description {
			position: relative;
			margin: 0 auto 5rem;
			z-index: 2;
			text-align: center;
			@include max-lg {
				margin-bottom: 3rem;
			}
		}
		.venue-content {
			position: relative;
			.ribbon-venue {
				position: relative;
				padding-top: 1rem;
				svg {
					width: 40px;
					height: auto;
					path {
						stroke: var(--color-dark);
					}
				}
			}
			.card {
				height: 100%;
				z-index: 5;
				text-align: center;
				overflow: hidden;
				border: none;
				background: $stroke-gradient;
				padding: 1px;
				mask: var(--mask);
				.card-body {
					position: relative;
					background: var(--color-main);
					padding: 3rem 2rem;
					mask: var(--mask);
					@include max-lg {
						padding: 2rem 1rem;
					}
					h4 {
						margin: 1rem 0;
					}
					hr {
						display: block;
						width: 10%;
						height: 2px;
						margin: 0 auto 1rem;
					}
					.elements-wrapper-absolute {
						mask: var(--mask);
					}
				}
			}
		}
	}
	.prokes-wrapper {
		margin: 3rem 0 0;
		position: relative;
		padding: 2rem;
		text-align: center;
		z-index: 2;
		.title-section {
			&::before {
				all: unset;
			}
		}
		@include max-md {
			padding: 2rem 0;
		}
		* {
			position: relative;
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 45%;
			}
		}
	}
	.row-venue {
		@include max-md {
			gap: 3rem;
		}
	}
	.event-name {
		position: relative;
		width: 100%;
		.date {
			position: relative;
			width: 100%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			h5 {
				font-family: $font-secondary;
				font-weight: 700;
				margin-bottom: 0;
				font-size: 48px;
				min-width: 30%;
			}
		}
		h4 {
			font-family: $font-secondary;
		}
		p {
			margin-bottom: 0;
			font-weight: 600;
		}
	}
	.event-place {
		position: relative;
	}
	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}
		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}
		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}
		40% {
			opacity: 1;
		}
		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}
		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}
		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
