.story-section {
	position: relative;
	.story {
		position: relative;
		padding: 5rem 1.2rem 3rem;
		@include max-md {
			padding: 3rem 1.2rem;
		}
		.story-body {
			position: relative;
			background: $stroke-gradient;
			padding: 1px;
			mask: var(--mask);
			.story-content {
				position: relative;
				background: var(--color-main);
				mask: var(--mask);
				.elements-wrapper-absolute {
					mask: var(--mask);
				}
			}
		}
	}
	.story-description {
		position: relative;
		text-align: center;
		padding: 3rem 2rem;
		@include max-md {
			padding: 2rem 0rem;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		@include max-md {
			aspect-ratio: 6/4;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}
	}
}
