.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	height: 100vh;
	width: 100%;
	&.cover-opened {
		transform: translateY(-100%);
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		transition: opacity 1s ease;
		@include max-lg {
			padding: 4rem 0 6rem;
			align-items: center;
		}
		.cover-content {
			text-align: center;
			position: relative;
			background-color: transparent;
			border-radius: 12px;
			color: var(--color-dark);
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			.cover-body {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				padding: 2rem;
				color: var(--color-light);
				@include max-lg {
					padding: 3rem 2rem 2rem;
					flex-direction: column;
				}
				@include max-xs {
					padding: 2rem 1rem 1rem;
					transform: scale(0.8);
				}
				&::before {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background: var(--color-primary);
					opacity: 0.6;
					content: "";
					mask: var(--mask);
				}
				.greeting-wrapper {
					position: relative;
					@include max-md {
						font-size: 14px;
					}
					h6 {
						font-weight: 700;
					}
				}
				h6 {
					font-weight: 600;
				}
				.image-wrapper {
					position: relative;
					width: 240px;
					height: auto;
					aspect-ratio: 3/4;
					background: $stroke-gradient;
					padding: 1px;
					mask: var(--mask);
					@include max-lg {
						width: 50%;
						margin: 0 auto;
						aspect-ratio: 7/8;
					}
					@include max-md {
						width: 90%;
					}
					@include max-xs {
						width: 90%;
					}
					.cover-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
						position: relative;
						mask: var(--mask);
					}
				}
				.cover-description {
					padding: 0 2rem;
					text-align: center;
					@include max-lg {
						padding: 2rem 0 1rem;
					}
					* {
						position: relative;
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
