.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.appology {
		position: relative;
		padding: 1px;
		background: $stroke-gradient;
		mask: var(--mask);
		&-body {
			position: relative;
			background: var(--color-main);
			mask: var(--mask);
			.elements-wrapper-absolute {
				mask: var(--mask);
			}
		}
		&-content {
			position: relative;
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}
}
